import React, { useState } from "react";
import { FaQrcode, FaCamera } from "react-icons/fa";

function GoGasProcess2() {
  const [step, setStep] = useState(1);

  return (
    <div className="min-h-screen flex items-center justify-center p-4">
      <div className="bg-white shadow-lg rounded-2xl p-6 w-full max-w-md">
        <h1 className="text-xl font-semibold text-purple-700 mb-4 text-center">
          GoGas coupons redemption with camera
        </h1>

        <div className="w-full bg-gray-200 rounded-full h-2.5 mb-4">
          <div
            className={`bg-purple-600 h-2.5 rounded-full ${
              step === 1 ? "w-1/2" : "w-full"
            }`}
          ></div>
        </div>
        <p className="text-right text-sm text-gray-600 mb-4">
          Page {step} of 2
        </p>

        {step === 1 && (
          <>
            <div className="mb-6">
              <label className="block text-black-700 text-md font-medium mb-2">
                1. Scan QR
              </label>
              <div className="flex justify-center">
                <button className="flex items-center justify-center bg-yellow-400 hover:bg-yellow-500 text-white font-bold py-2 px-4 rounded-lg transition duration-300 w-24">
                  <FaQrcode className="mr-2" /> Scan
                </button>
              </div>
            </div>

            <div className="mb-4">
              <label className="block text-black-700 text-md font-medium mb-2">
                2. Coupon code <span className="text-red-500">*</span>
              </label>
              <input
                type="text"
                placeholder="Enter coupon code"
                className="w-full border border-gray-300 p-2 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-600"
              />
            </div>

            <div className="mb-4">
              <label className="block text-black-700 text-md font-medium mb-2">
                3. Cash To Be Collected <span className="text-red-500">*</span>
              </label>
              <input
                type="text"
                placeholder="Enter amount"
                className="w-full border border-gray-300 p-2 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-600"
              />
            </div>

            <button
              onClick={() => setStep(2)}
              className="w-full bg-purple-700 hover:bg-purple-800 text-white font-bold py-2 px-4 rounded-lg transition duration-300"
            >
              Next
            </button>
          </>
        )}

        {step === 2 && (
          <>
            <div className="mb-6">
              <label className="block text-black-700 text-md font-medium mb-2">
                4. Amount To Be Mentioned in Despenser
              </label>
              <p className="text-black-500">552</p>
            </div>

            <div className="mb-6">
              <label className="block text-black-700 text-md font-medium mb-2">
                5. Take a picture of the dispenser after dispensing{" "}
                <span className="text-red-500">*</span>
              </label>
              <div className="flex justify-center">
                <button className="flex items-center justify-center bg-yellow-400 hover:bg-yellow-500 text-white font-bold p-3 rounded-full transition duration-300">
                  <FaCamera className="text-xl" />
                </button>
              </div>
            </div>

            <div className="mb-4">
              <label className="block text-black-700 text-md font-medium mb-2">
                6. Maximum Extraa Amount (in Rs)
              </label>
              <p className="text-black-500">52</p>
            </div>

            <div className="flex justify-between">
              <button
                onClick={() => setStep(1)}
                className="bg-white border border-purple-700 text-purple-700 font-bold py-2 px-4 rounded-lg transition duration-300 hover:bg-purple-50"
              >
                Previous
              </button>
              <button className="bg-purple-700 hover:bg-purple-800 text-white font-bold py-2 px-4 rounded-lg transition duration-300">
                Complete
              </button>
            </div>
          </>
        )}

        <div className="text-center mt-4 text-sm text-purple-600">
          Forms by <span className="font-bold text-yellow-700">extraa</span>
        </div>
      </div>
    </div>
  );
}

export default GoGasProcess2;
